import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import Tabel from "../../../components/helpers/tabel.jsx";
import { localStorageencryp } from "../../../components/helpers/function.jsx";
import { getData } from "../../../config/axios.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};
class FormValidasiPenjualan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      formEdit: false,
      loadingData: false,
      FormValidasiPenjualan: [],
      columns: [
        {
          dataField: "kode_barang",
          text: "Kode Barang ",
          sort: true,
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "berat",
          text: "Berat",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }
  getKodeBarcode(e) {
    localStorageencryp("databarang_validasi", JSON.stringify([]));
    this.setState({ loadingData: true });
    getData("barang/get/by-kode-barcode/" + e)
      .then((res) => {
        if (res.data.length === 0) {
          this.setState({ FormValidasiPenjualan: [] });
        } else {
          this.setState({ FormValidasiPenjualan: res.data });
        }
        this.setState({ loadingData: false });
      })
      .catch((err) => {
        this.setState({ loadingData: false });
        ToastNotification("info", "Kode Barcode Yang Anda Dicari Tidak Ada");
      });
  }
  getNamaBarang(e) {
    localStorageencryp("databarang_validasi", JSON.stringify([]));
    this.setState({ loadingData: true });
    getData("barang/get/by-nama-barang/" + e)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          this.setState({ FormValidasiPenjualan: [] });
        } else {
          this.setState({ FormValidasiPenjualan: res.data });
        }
        this.setState({ loadingData: false });
      })
      .catch((err) => {
        this.setState({ loadingData: false });
        localStorageencryp("databarang_validasi", JSON.stringify([]));
        ToastNotification("info", "Nama Barang Yang Dicari Tidak Ada");
      });
  }
  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Nama Atribut : {row.nama_atribut} <br />
              Betat Atribut : {row.berat_atribut} <br />
              Harga Atribut : {row.harga_atribut} <br />
              Kode Intern : {row.kode_intern} <br />
              Kadar : {row.kadar} <br />
              Kadar Cetak : {row.kadar_cetak} <br />
              Kode Toko : {row.kode_toko} <br />
              Kode Gudang : {row.kode_gudang} <br />
              Stock : {row.stock_on_hand} <br />
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
      expandByColumnOnly: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Penjualan</Link>
          </li>
          <li className="breadcrumb-item active">Validasi Penjualan</li>
        </ol>
        <h1 className="page-header">Validasi Penjualan </h1>

        <Panel>
          <PanelHeader>
            {this.state.loadingData ? (
              <>
                {" "}
                <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang Mencari
                Validasi Penjualan{" "}
              </>
            ) : (
              "Validasi Penjualan"
            )}
          </PanelHeader>
          <br />

          {/* {console.log(localStorage.getItem('FormValidasiPenjualanall'))} */}
          {/* Validasi Penjualan */}
          <div className="container">
            <Tabel
              keyField="kode_barang"
              exportCsv={true}
              data={this.state.FormValidasiPenjualan}
              columns={this.state.columns}
              empty={true}
              textEmpty={
                this.state.loadingData ? (
                  <>
                    {" "}
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari Data
                  </>
                ) : (
                  "Validasi Penjualan Kosong"
                )
              }
              expandRow={expandRow}
            />
          </div>
          <br />
          {/* End Validasi Penjualan */}
         
          {/* End Tambah Validasi Penjualan  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormValidasiPenjualan);
